import gql from "graphql-tag";
import {ChildDataProps} from "react-apollo";
import {IFindImage} from "./interfaces";

export const findImages= (category: string): ChildDataProps<{}, IFindImage[], {}> => gql`
    {
        findImagesByCategory(lookingBy: "${category}") {
            id,
            name,
            path,
            category,
            description,
            images {
                name,
                path,
            }
        }
    }
`;

