import * as React from "react";
import { Query } from "react-apollo";
import {Row, Image, Container} from "react-bootstrap";
import Lightbox from "react-image-lightbox";
import {findImages, IFindImage} from "../../requests";
import {imageServer} from "../../config";
import {IPropsGallery, IStateGallery} from "./interfaces";

export class Gallery extends React.Component<IPropsGallery, IStateGallery> {
    public state: IStateGallery = {
        isOpen: false,
        index: 0,
        name: "",
    };

    public render() {
        return (
            <Query query={findImages(this.props.category)}>
                {({loading, error, data})=> {
                    if (loading) return <div className="loader"></div>;
                    if (error) return <div className="error">{error.message}</div>
                    const {index, isOpen, name} = this.state;

                    return (
                        <Container>
                            <Row>
                                {data.findImagesByCategory.map((image: IFindImage, i: number) => {
                                    const imgs = image.images.map((img) => `${imageServer}/${img.path}`);

                                    return(
                                        <div key={i} className="image-container col-12 col-sm-6 col-md-6 col-lg-4 col-xl-4">
                                            <div
                                                className="container-description"
                                                id={`description-${i}`}
                                                onClick={() => this.setState({
                                                    isOpen: true,
                                                    name: `${image.name}-${image.category}`
                                                })}>
                                                <p className="description">{image.description}</p>

                                            </div>
                                            <Image
                                                key={image.id}
                                                className="cover"
                                                id={`cover-${i}`}
                                                src={`${imageServer}/${image.path}`}
                                            />

                                            {isOpen && (name === `${image.name}-${image.category}`) ?
                                                <Lightbox
                                                    key={i}
                                                    mainSrc={imgs[index] ? imgs[index]: imgs[0]}
                                                    nextSrc={(imgs.length > 1) ? imgs[(index + 1) % imgs.length] : undefined}
                                                    imageCaption={image.description}
                                                    imageTitle={image.name.toUpperCase().replace(/[#_]/g," ")}
                                                    prevSrc={(imgs.length > 1) ? imgs[(index + imgs.length - 1) % imgs.length]: undefined}
                                                    onCloseRequest={() => this.setState({ isOpen: false })}
                                                    onMovePrevRequest={() => 
                                                        this.setState({
                                                            index: (index + imgs.length -1) % imgs.length,
                                                        })
                                                    }
                                                    onMoveNextRequest={() => 
                                                        this.setState({
                                                            index: (index + 1) % imgs.length,
                                                        })
                                                    }
                                                    ></Lightbox>
                                            : ""}
                                        </div>
                                    )
                                    
                                }
                                    
                                )}
                            </Row>
                        </Container>
                    );
                }}
            </Query>
        );
    }
}
