import * as React from  "react";
import {ApolloProvider} from "react-apollo";
import {Navbar, Nav} from "react-bootstrap";
import {client} from "../config";
import {State} from "./interfaces";
import {Gallery} from "../components";
import instagram from "../assets/ig.png";
import linkedin from "../assets/linkin.png";

/**
 * Bussines component page
 */
export class Page extends React.Component<any, State> {
    public state: State = {
        activeItem: "All Works",
    };
    private items = ['All Works', "Printing", "Packaging", "Logo", "Other"]
    private links = ['all-works', "printing", "packaging", "logo", "other"]
    public render() {
        return (
            <div className="page">
                <div className="portfolio"></div>
                <div className="portfolio-intro"></div>
                <ApolloProvider client={client}>
                    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
                        <Navbar.Brand onClick={() => {
                            location.reload();
                        }}>
                            <span className="branch">Gloria Kwok</span>
                        </Navbar.Brand>
                        <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
                        <Navbar.Collapse id="responsive-navbar-nav">
                            <Nav className="mr-auto">
                                {this.items.map((item, index) => (item !== "Contact") ?
                                    <Nav.Link key={`#${this.links[index]}`}
                                        href={`#${this.links[index]}`}
                                        className={item === this.state.activeItem ? "active" : ""}
                                        onClick={() => this.setState({activeItem: item})}
                                    >{item}</Nav.Link> :
                                    <Nav.Link key={item}
                                        href={`#${this.links[index]}`}
                                        onClick={() => this.setState({activeItem: item})}
                                    >{item}</Nav.Link>
                                )}
                            </Nav>
                        </Navbar.Collapse> 
                        <Navbar.Brand href="https://www.instagram.com/mein.zweites.leben/" target="_blank">
                            <img
                                alt="instagram"
                                src={instagram}
                                width="30"
                                height="30"
                                className="d-inline-block align-top"
                            ></img>
                        </Navbar.Brand>
                        <Navbar.Brand href="https://www.linkedin.com/in/gloriakwn/" target="_blank">
                            <img
                                alt="linkedin"
                                src={linkedin}
                                width="30"
                                height="30"
                                className="d-inline-block align-top"
                            ></img>
                        </Navbar.Brand>
                    </Navbar>
                    <div className="gallery-container">
                        <Gallery 
                            category={this.state.activeItem.toLowerCase()}
                        />
                    </div>
                </ApolloProvider>
            </div>
        );
    }
}